<template>
    <transition name="mine-confirm">
      <div class="mine-confirm-wrapper" v-show="visible">
        <div class="mine-confirm">
          <div class="mine-confirm-title">{{title}}</div>
          <div class="mine-confirm-msg">{{msg}}</div>
          <div class="mine-confirm-btns">
            <button
              class="mine-confirm-btn mine-confirm-cancel"
              @click="cancel"
            >{{cancelBtnText}}</button>
            <button
              class="mine-confirm-btn mine-confirm-ok"
              @click="confirm"
            >{{confirmBtnText}}</button>
          </div>
        </div>
      </div>
    </transition>
</template>

<script>
  export default {
    name: 'MeConfirm',
    props: {
      title: {
        type: String,
        default: ''
      },
      msg: {
        type: String,
        default: '确定执行此操作吗?'
      },
      cancelBtnText: {
        type: String,
        default: '取消'
      },
      confirmBtnText: {
        type: String,
        default: '确定'
      }
    },
    data() {
      return {
        visible: false
      };
    },
    methods: {
      show() {
        this.visible = true;
      },
      hide() {
        this.visible = false;
      },
      cancel() {
        this.hide();
        this.$emit('cancel');
      },
      confirm() {
        this.hide();
        this.$emit('confirm');
      }
    }
  };
</script>

<style lang="scss" scoped>
  @import "~assets/scss/mixins";

  .mine-confirm-wrapper{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: $search-popup-z-index;
    @include flex-center();
    background-color: $modal-bgc;
  }
  .mine-confirm {
    overflow: hidden;
    width: 88%;
    background-color: #fff;
    border-radius: 10px;
    font-size: 16px;

    &-title {
      padding: 20px 15px 0;
      font-size: 18px;
      text-align: center;
      @include ellipsis();
    }
    &-msg {
      padding: 20px 15px 30px;
      text-align: center;
    }
    &-btns {
      display: flex;
      height: 44px;
      line-height: 44px;
      background: none;
      border: none;
    }
    &-cancel {
      flex: 1;
      border-top: 1px solid #e3e5e9;
      background-color: #fff;
    }

    &-ok {
      flex: 1;
      background-color: #f23030;
      color: #fff;
    }

    &-enter-active,
    &-leave-active {
      transition: opacity 0.3s;
    }

    &-enter,
    &-leave-to {
      opacity: 0;
    }

    &-enter-active {
      .mine-confirm {
        animation: bounce-in 0.3s;
      }
    }
  }

  @keyframes bounce-in {
    0% {
      transform: scale(0);
    }
    50% {
      transform: scale(1.1);
    }
    100% {
      transform: scale(1);
    }
  }

</style>



